.App {
  text-align: center;
  font-family: Roboto, sans-serif;
}

.navbar {
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center; /* Center items vertically */
  padding: 15px 15px;
  border-width: 0px 0px 5px 0px;
  border-color: #4e3d1a;
  border-style: solid;
  padding-right: 30px;
}

.route-link {
  font-size: large;
  font-weight: bold;
}

.route-link a {
  color: white !important;
  text-decoration: none !important
}

.route-link a:visited {
  color: white !important; /* Style for visited links inside .route-link */
  text-decoration: none !important
}

.navbar-title a {
  color: white !important;
  text-decoration: none !important
}


.navbar-title a:visited {
  color: white !important; /* Style for visited links inside .route-link */
  text-decoration: none !important
}

.navbar-title {
  font-size: 30px;
  color: white;
}
